.header{
    display: flex;
    height: 100vh;
    background-color: hotpink;
}

.container{
    overflow: hidden;
}

.header-menu{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-right: 3rem;
    padding: 10px;
    font-size: 1.2rem;
    color: #fff;
    z-index: 10;
    text-decoration: none;
    list-style: none;
    overflow: hidden;
}

.menu:hover{
    transition: ease-in-out 0.3s;
    cursor: pointer;
    text-decoration: underline;
    color: darkmagenta;
}

.menu{
    text-decoration: none;
    list-style: none;
    color: white;
    overflow: hidden;

}

.header-menu>li{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 60px;
    list-style: none;
    height: fit-content;
    border-radius: 50%;
    padding: 10px;
    width: fit-content;
    font-weight: bold;
    text-decoration: none;
    color: white;
    list-style: none;
    overflow: hidden;

}



.hero{
    position: absolute;
    top: 30%;
    left: 140px;
    font-size: 3rem;
    color: #fff;
    z-index: 10;
    width: fit-content;
    height: fit-content;
    border: 3px solid lightpink;
    padding: 15px;
    font-weight: bold;
    overflow: hidden;

}

.logo{
    width: 160px;
    height: 160px;
    z-index: 10;
    overflow: hidden;
}

.logo2{
    padding: 20px;
    width: 100px;
    height: 100px;
    z-index: 10;
    overflow: hidden;
}

.logo1{
    position: absolute;
    top: 100px;
    left: 65%;
    font-size: 3rem;
    color: #fff;
    z-index: 10;
    width: 400px;
    height: 515px;
    animation: slideUp 1.2s ease-in;
    overflow: hidden;

}

.flag{
    position: absolute;
    top: 100px;
    left: 65%;
    font-size: 3rem;
    color: #fff;
    z-index: 9;
    width: 400px;
    height: 515px;
    border-radius: 20px;
    overflow: hidden;

}

.info-container{
    display: flex;
    gap: 2rem;
    width: 800px;
    height: 200px;
    background-color: white;
    position: absolute;
    top: 60%;
    z-index: 10;
    border-radius: 10px;
    animation: slideMe 0.9s ease-in;
    overflow: hidden;

}

@keyframes slideMe{
    0%{
        transform: skewX(53deg) translateX(1000px);
    }
    100%{
        transform: skew(0deg);
    }
}

@keyframes slideUp {
    0%{
        transform: translateY(700px);
    }
}


.support{
    position: absolute;
    top: 60%;
    color: hotpink;
    z-index: 10;
    font-size: 1.25rem;
    padding: 20px;
    text-indent: 40px;
    width: 760px;
}

@media (max-width: 600px){
    .header-menu{
        margin-right: 1rem;
        padding: 0px;
        font-size: 0.8rem;
    }

    .header-menu>li{
        margin-right: 10px;
        padding: 8px;
    }

    .logo2{
        padding: 10px;
        width: 70px;
        height: 70px;
    }

    .hero{
        top: 15%;
        left: 43px;
        font-size: 2rem;
        padding: 10px;
    }

    .logo1{
        top: 180px;
        left: 10%;
        z-index: 9;
        width: 310px;
        height: 405px;
    }

    
    .flag{
        position: absolute;
        top: 180px;
        left: 10%;
        font-size: 3rem;
        color: #fff;
        z-index: 8;
        width: 315px;
        height: 405px;
        border-radius: 20px;
    }

    .info-container{
        width: 385px;
        height: 200px;
        top: 70%;
        overflow: hidden;
    }

    
    .support{
        position: absolute;
        top: 69%;
        color: hotpink;
        z-index: 10;
        font-size: 0.9rem;
        padding: 20px;
        text-indent: 40px;
        width: 350px;
        overflow: hidden;
    }
}


