.events-container{
    font-size: 0.8rem;
    z-index: 10;
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 100px;
    top: 20%;
    left: 70px;
    color: white;
}

.zaddy{
    height: 500px;
    animation: slideUp 1.2s ease-in;
}

.bg-2{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 500px;
    background: #000;
    opacity: 0.25;
    z-index: 1;
    border-radius: 10px;
}
.event{
    width: 80%;
    height: 500px;
    background: #ff61b0;
    z-index: 11;
    top: 18%;
    left: 10%;
    position: absolute;
    border-radius: 10px;
}

.event-1:hover{
    background-color: rgba(255, 255, 255, 0.4);
    border: 2px solid black;
    color: black;
    transition: ease-in-out 0.3s;
}

.event-2:hover{
    background-color: rgba(255, 255, 255, 0.4);
    border: 2px solid black;
    color: black;
    transition: ease-in-out 0.3s;
}

.event-3:hover{
    background-color: rgba(255, 255, 255, 0.4);
    border: 2px solid black;
    color: black;
    transition: ease-in-out 0.3s;
}

.event-4:hover{
    background-color: rgba(255, 255, 255, 0.4);
    border: 2px solid black;
    color: black;
    transition: ease-in-out 0.3s;
}


.event-1>:nth-child(1){
    font-size: 1rem;
    font-weight: bold;
    text-decoration: underline;
}

.event-2>:nth-child(1){
    font-size: 1rem;
