@keyframes slideUp {
    0%{
        transform: translateY(700px);
    }
}


@media (max-width: 600px){
    .events-container{
        grid-template-columns: 1fr;
        grid-gap: 34px;
        top: 0%;
        left: 0px;
    }

    .event{
        top: 17%;
    }

    .event-2{
        width: 288px;
        padding: 8px;
        height: 80px;
    }

    .event-3{
