        padding: 8px;
        height: 80px;
    }

    .event-4{
        width: 288px;
        padding: 8px;
        height: 80px;
    }

    .event-2>:nth-child(1){
        font-size: 0.8rem;
    }
    
    .e{
        top: -15%;
        left: 110px;
        font-size: 2rem;
        z-index: 11;
    }

    
    .zaddy{
