
.about-container{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50vh;
    left: 0;
    z-index: 0;
    padding: 40px;
    gap: 3rem;
    width: 600px;
    background-color: white;
    height: 200px;
    border-radius: 10px;
    animation: slideMe 0.9s ease-in;
}
.abt{
    display: flex;
    justify-content: center;
}

.about{
    color:white;
    position: absolute;
    top: 22%;
    left: 200px;
    z-index: 10;
    text-decoration: underline;
    font-size: 3rem;
}

.student-debt{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    font-size: 0.9rem;
    gap: 2rem;
    font-weight: bold;
}

.bg-3{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.1);
}

.minimum-wage{
    display:flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    font-size: 0.9rem;
    gap: 2rem;
    font-weight: bold;

}

.foriegn-aid{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    font-size: 0.9rem;
    gap: 2rem;
    font-weight: bold;

}


.img-1{
    position: absolute;
    top: 60vh;
    left: 200px;
    z-index: 10;
    width: 300px;
    height: 200px;
    opacity: 0.5;
}

.img-2{
    position: absolute;
    top: 19px;
    right: 0;
    animation: slideUp 1.2s ease-in;
}

@keyframes slideMe{
    0%{
        transform: skewX(53deg) translateX(-1000px);
    }
    100%{
        transform: skew(0deg);
    }
}

@keyframes slideUp {
    0%{
        transform: translateY(700px);
    }
}


@media (max-width: 600px){

    .about-container{
        top: 60.5vh;
        left: 0;
        z-index: 10;
        padding: 20px;
        gap: 1rem;
        width: 90%;
        height: 200px;
    }

    .img-2{
        position: absolute;
        top: 109px;
        left: 40px;
        width: 300px;
        z-index: 9;
    }

    .about{
        top: 5%;
        left: 110px;
        font-size: 2.4rem;
    }

    .student-debt{
        font-size: 0.73rem;
        gap: 0.5rem;
    }

    .minimum-wage{
        font-size: 0.75rem;
        gap: 1rem;
    }

    .foriegn-aid{
        font-size: 0.8rem;
        gap: 1rem;
    }

    .img-1{
        position: absolute;
        top: 65vh;
        left: 40px;
        z-index: 10;
        width: 300px;
        height: 200px;
        opacity: 0.5;
    }
}